import {wfdList1} from "../utils/wfd-list";
import {getSpeech} from "../utils/utils";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";

const WfdStudy = () => {
  const [currentSentence, setCurrentSentence] = useState("");
  const [currentIdx, setCurrentIdx] = useState(0);
  const [answer, setAnswer] = useState("");
  const [rightAnswer, setRightAnswer] = useState(0);
  const [showAnswer, setShowAnswer] = useState(0);
  const [showFront, setShowFront] = useState(false);

  //   preload
  useEffect(() => {
    window.speechSynthesis.getVoices();
    setCurrentSentence(wfdList1[currentIdx]);
  }, []);

  const playButton = () => {
    console.log(currentSentence);
    getSpeech(currentSentence);
  };

  const answerInput = (e) => {
    const { value } = e.target;
    setAnswer(value);
  };

  const moveQuestion = (target) => {
    let nextTarget = currentIdx
    if (target === 'next') {
      nextTarget += 1;
    } else if (target === 'prev') {
      nextTarget -= 1;
    } else {
      if (isNaN(target)) {
        alert(" .");
        return;
      }
      if (target === '') {
        alert(" .");
        return;
      }
      nextTarget = parseInt(target) - 1;
    }

    console.log(nextTarget);

    if (nextTarget >= wfdList1.length) {
      alert(" .");
      nextTarget = 0;
    }
    if (nextTarget < 0) {
      alert(" .");
      nextTarget = 0;
    }
    setShowAnswer(0);
    setCurrentIdx(nextTarget);
    setCurrentSentence(wfdList1[nextTarget]);
  }

  const answerCheck = () => {
    if (showAnswer === 0) {
      setShowAnswer(1);
    } else {
      setShowAnswer(0);
    }

    if (answer === currentSentence) {
      setRightAnswer(1);
    } else {
      setRightAnswer(0);
    }
  }

  return (
    <div className="App">
      <ul style={{listStyle: 'none'}}>
        <li><Link to={'/'}>WFD() </Link></li>
        <li><Link to={'/wfdv2'}>WFDv2() </Link></li>
        <li><Link to={'/we'}>WE() </Link></li>
        <li><Link to={'/wev2'}>WEv2() </Link></li>
        <li><Link to={'/sst'}>SST() </Link></li>
        <li><Link to={'/rs'}>RS() </Link></li>
        <li><Link to={'/asq'}>ASQ() </Link></li>
      </ul>
      <h1>WFD()</h1>
      <div className="box">
        <button onClick={playButton}> </button>
        <div>
           <input type={"checkbox"} onChange={(event) => setShowFront(event.target.checked)} />
          <span style={{display: showFront? 'block' : 'none'}}>
            {`${currentSentence.split(' ')[0]} ${currentSentence.split(' ')[1]}`}
          </span>
        </div>
        <div>  <span style={{color: 'red', fontWeight: 'bold', fontSize: '15px'}}>{parseInt(currentIdx)+1}</span></div>
        <div>
          <input onChange={answerInput} value={answer} autoComplete="off" style={{width: '600px'}} />
        </div>
        <br />
        <button onClick={answerCheck}> </button>
        {/*<div style={{display: showAnswer === 0 ? 'none' : 'block'}}><span>: {currentSentence}</span></div>
        <div style={{display: showAnswer === 0 ? 'none' : 'block'}}><span> : {answer}</span></div>*/}
        <div style={{display: showAnswer === 0 ? 'none' : 'block', textAlign: 'left'}}>
          <div style={{width: '800px', margin: '0 auto'}} >
            <span>: {currentSentence}</span><br />
            <span>: {answer}</span>
          </div>
        </div>
        <br />
        <div><span>{rightAnswer === 0 ? '' : ''}</span></div>
        <br />
        <button onClick={() => moveQuestion('prev')}> </button>
        <button onClick={() => moveQuestion('next')}> </button>
        <br/>
        <label htmlFor={'jump'}> : <input onChange={(event) => moveQuestion(event.target.value)} name={'jump'} /></label>
      </div>
    </div>
  );
};

export default WfdStudy;