import {getSpeech} from "../utils/utils";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {RsList1} from "../utils/rs-list";
import {wfdList1} from "../utils/wfd-list";

const RsStudy = () => {
  // 110  
  const [answer, setAnswer] = useState([]);
  const [rightAnswer, setRightAnswer] = useState([]);
  const [showAnswer, setShowAnswer] = useState([]);
  const [studyMode, setStudyMode] = useState(false); // wfd, we
  const [voiceMode, setVoiceMode] = useState(true); // wfd, we
  // const [showFront, setShowFront] = useState(false);

  //   preload
  useEffect(() => {
    const defaultValue = [];
    const defaultRightAnswer = [];
    for (let i = 0; i < RsList1.length; i++) {
      defaultValue.push('');
      defaultRightAnswer.push(false)
    }
    setAnswer(defaultValue);
    setRightAnswer(defaultRightAnswer);
    setShowAnswer(defaultRightAnswer);
    window.speechSynthesis.getVoices();
  }, []);

  const playButton = (targetIdx) => {
    const currentSentence = RsList1[targetIdx];
    getSpeech(currentSentence);
  };

  const answerInput = (value, idx) => {
    const newAnswer = [...answer];
    newAnswer[idx] = value;
    setAnswer(newAnswer);
  };

  const enterDown = (e, idx) => {
    if (e.key === 'Enter') {
      // console.log('ENTER?????');
      answerCheck(idx);
    }
  }

  const answerCheck = (idx) => {
    const newShowAnswer = [...showAnswer];
    const newRightAnswer = [...rightAnswer];
    if (showAnswer[idx] === false && voiceMode === true) {
      playButton(idx);
    }
    newShowAnswer[idx] = !showAnswer[idx];
    setShowAnswer(newShowAnswer);

    if (answer[idx] === RsList1[idx]) {
      newRightAnswer[idx] = true;
    } else {
      newRightAnswer[idx] = false;
    }
    setRightAnswer(newRightAnswer);
  }

  const sleep = ms => new Promise(r => setTimeout(r, ms));
  const playAllRs = async () => {
    for (let i = 0; i < RsList1.length; i++) {
      playButton(i);
      await sleep(2000);
    }
  };

  return (
    <div className="App">
      <ul style={{listStyle: 'none'}}>
        <li><Link to={'/'}>WFD() </Link></li>
        <li><Link to={'/wfdv2'}>WFDv2() </Link></li>
        <li><Link to={'/we'}>WE() </Link></li>
        <li><Link to={'/wev2'}>WEv2() </Link></li>
        <li><Link to={'/sst'}>SST() </Link></li>
        <li><Link to={'/rs'}>RS() </Link></li>
        <li><Link to={'/asq'}>ASQ() </Link></li>
      </ul>
      <h1>RS</h1>
      <h2>110   </h2>
      <div className="box">
        <div>
           <input type={"checkbox"} onChange={(event) => setStudyMode(event.target.checked)} checked={studyMode} /><br />
           <input type={"checkbox"} onChange={(event) => setVoiceMode(event.target.checked)} checked={voiceMode} /><br />
          <button onClick={playAllRs}>  </button>
        </div>
        <br />
        <table style={{border: '1px solid black', borderCollapse: 'collapse', marginLeft: 'auto', marginRight: 'auto'}}>
          <colgroup>
            <col width="20px" />
            <col width="50px" />
            <col width="650px" />
            <col width="70px" />
          </colgroup>
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          {RsList1.map((item, idx) => {
            return (
              <tr key={idx}>
                <td>{idx + 1}</td>
                <td><span>{rightAnswer[idx] === false ? '' : ''}</span></td>
                <td style={{textAlign: 'left'}}>
                  <div>
                    {studyMode ? item:``}
                  </div>
                  <div>
                    <input type='text' onChange={(e) => answerInput(e.target.value, idx)} onKeyDown={(e) => enterDown(e, idx)} value={answer[idx]} autoComplete="off" style={{width: '600px', fontSize: '16px'}} />
                  </div>
                  <div style={{display: showAnswer[idx] === false ? 'none' : 'block', textAlign: 'left'}}>
                    <div style={{width: '600px'}} >
                      <span>: {item}</span><br />
                      <span>: {answer[idx]}</span>
                    </div>
                  </div>
                </td>
                <td><button onClick={() => playButton(idx)}></button></td>
              </tr>
            )
          })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default RsStudy;